.wrapper > * {
    flex: 0 0 auto;
}

a {
    text-decoration: none;
    background-color: transparent;
}

.gy-5 {
    font-size: 14px;
}

.py-8 {
    padding-top: 3.5rem !important;
    padding-bottom: 3.5rem !important;
}

.footer-link-01 li + li {
    padding-top: 0.8rem;
}

.footer-title-01 {
    font-size: 25px;
    margin: 0 0 20px;
    font-weight: 600;
}

.footer-title-02 {
    font-size: 18px;
    margin: 0 0 20px;
    font-weight: 600;
    padding-top: 10px;
}

@media (max-width: 991.98px) {
    .footer-link-01 li + li {
        padding-top: 0.6rem;
    }
}

.footer-link-01 {
    color: white;
}

.footer-link-01 a {
    position: relative;
    display: inline-block;
    vertical-align: top;
}

.footer-link-01 a:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: auto;
    right: 0;
    width: 0;
    height: 1px;
    transition: ease all 0.35s;
    background: currentColor;
}

.footer-link-01 a:hover:after {
    left: 0;
    right: auto;
    width: 100%;
}

.ftcontainer {
    margin-left: 9%;
}

.campuses {
    margin-top: 97px;
}
